:root {
	/* Color Palette */
	--color-white: #fff;
	--color-off-white: #aaa;
  --color-light: #eee;
	--color-light-gray: #999;
	--color-grey: #a3a3a3;
	--color-dark-gray: #373a3e;
	--color-black: #111;
  --color-dark: #4a00cc;
	--color-red: #dc0005;
  --color-deep-orange: #ff2800;
  --color-orange: #ff7a00;
  --color-amber: #ffaf00;
  --color-yellow: #fadc00;
  --color-lime: #dfdc00;
  --color-light-green: #95cc0d;
  --color-green: #13b20b;
  --color-teal: #339985;
  --color-cyan: #00b1cc;
  --color-light-blue: #6da7d1;
  --color-blue: #0d7ecc;
	--color-dark-blue: #3b4eb8;
  --color-indigo: #3b4eb8;
  --color-deep-purple: #639;
  --color-purple: #c30c70;
  --color-pink: #eb2f59;
  --color-brown: #7a5653;
  --color-blue-grey: #617c8f;
	--color-border: rgba(0, 0, 0, 0.1);

	--font-color: #373a3e;

	--font-color: var(--color-dark-gray);
	--font-color-hover: #111;
	--font-color-hover: var(--color-black);
	--font-color-inverse: #aaa;
	--font-color-inverse: var(--color-off-white);
	--font-color-inverse-hover: #fff;
	--font-color-inverse-hover: var(--color-white);
	--link-color: #ff2800;
	--link-color: var(--color-deep-orange);
	--link-color-hover: #eee;
	--link-color-hover: var(--color-light);

	--dark-background-color: #212329;
	--darker-background-color: #141414;

	/* Typography */
	--body-font-size: 15px;

	--block-quote-font-size: 15px;
	--block-quote-font-weight: 500;

	--pre-font-size: 13px;

	--header1-font-size: 25px;
	--header1-line-height: 1.2;

	--header2-font-size: 21px;
	--header2-line-height: 1.3;

	--header3-font-size: 19px;
	--header3-line-height: 1.4;

	--header4-font-size: 19px;
	--header4-line-height: 1.4;

	--header5-font-size: 17px;
	--header5-line-height: 1.4;

	--header6-font-size: 15px;
	--header6-line-height: 1.4;

	--table-first-column-font-size: 15px;

	--feature-points-font-size: 15px;

	/* App Variables */
	--border-radius: 4px;
	--grid-size: 10px;
	--card-padding: calc(10px * 2);
	--card-padding: calc(var(--grid-size) * 2);

	--header-background-color: #212329;

	--header-background-color: var(--dark-background-color);
	--coloresponsive-header-background-color: #141414;
	--coloresponsive-header-background-color: var(--darker-background-color);
	--hero-background: #17191c;
	--menu-offset: calc(10px * 2);
	--menu-offset: calc(var(--grid-size) * 2);
	--toggle-button-width: calc(10px * 2.6);
	--toggle-button-width: calc(var(--grid-size) * 2.6);
	--header-size: calc(10px * 6);
	--header-size: calc(var(--grid-size) * 6);

	--menu-height: calc(10px * 4.4);

	--menu-height: calc(var(--grid-size) * 4.4);

	--content-top-padding: calc(10px * 6);

	--content-top-padding: calc(var(--grid-size) * 6);
	--content-padding: calc(10px * 3);
	--content-padding: calc(var(--grid-size) * 3);
	--console-background-color: #141414;
	--console-background-color: var(--darker-background-color);
}

@media (max-width: 768px) {
	:root {
		--header1-font-size: 24px;
		--header2-font-size: 22px;
		--header3-font-size: 20px;
		--header4-font-size: 18px;
		--header5-font-size: 16px;
		--header6-font-size: 14px;
	}
}

@media (max-width: 850px) {
	:root {
		--header-size: calc(10px * 5);
		--header-size: calc(var(--grid-size) * 5);
		--content-top-padding: 10px;
		--content-top-padding: var(--grid-size);
		--content-padding: 10px;
		--content-padding: var(--grid-size);
	}
}

.root {
	--toggle-button-height: calc(10px * 2);
	--toggle-button-height: calc(var(--grid-size) * 2);
	--shadow-color: rgba(0, 0, 0, 0.5);
	--toggle-button-color: #d3d3d3;
	--logo-width: calc(10px * 9);
	--logo-width: calc(var(--grid-size) * 9);
	position: fixed;
	min-height: 56px;
	background: #212329;
	background: var(--header-background-color);
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	-ms-flex-line-pack: start;
	    align-content: flex-start;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	height: calc(10px * 6);
	height: var(--header-size);
	z-index: 10;
}

.menu {
	overflow: hidden;
}

.menuList {
	list-style: none;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 0;
	margin: 0;
	height: auto;
	width: auto;
	background: none;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	margin-right: 10px;
	margin-right: var(--grid-size);
}

.menuItem {
	font-size: 16px;
	line-height: 1.65;
	margin: 10px;
	margin: var(--grid-size);
	padding: calc(10px / 2) 0;
	padding: calc(var(--grid-size) / 2) 0;
	display: inline-block;
	text-decoration: none;
	position: relative;
	text-transform: uppercase;
}

.menuLink {
	color: #aaa;
	color: var(--font-color-inverse);
}

.menuLink:hover {
	color: white;
	text-decoration: none;
}

.inactive {
	text-decoration: none;
	opacity: .4;
}

.left {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-flex: 1;
	    -ms-flex: 1 0 auto;
	        flex: 1 0 auto;
	height: 100%;
}

.link, .link:link,
.link:visited {
	color: #aaa;
	color: #aaa;
	color: var(--font-color-inverse, #aaa);
	text-decoration: none;
	text-transform: uppercase;
}

.lang.link.selected, .lang.link.selected:link,
.lang.link.selected:visited {
	color: #ffaf00;
	color: #ffaf00;
	color: var(--color-amber, #ffaf00);
}

.lang.link.selected {
	cursor: s-resize;
}

.link::before {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	right: 100%;
	border-bottom: 1px solid #373a3e;
	border-bottom: 1px solid var(--color-dark-gray);
	-webkit-transition: right 0.3s ease-out;
	transition: right 0.3s ease-out;
}

.link:hover {
	color: #fff;
	color: var(--font-color-inverse-hover);
}

.link:hover::before {
	right: 0;
}

.link:hover {
	color: #fff;
	color: var(--font-color-inverse-hover);
}

.homeLink {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
}

.selected {
	color: white;
}

.logo {
	width: var(--logo-width);
	width: var(--logo-width);
}

.leftContainer {
	margin: auto calc(10px * 2);
	margin: auto calc(var(--grid-size) * 2);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.centerContainer {
	height: 100%;
}

.toggleButton {
	text-align: left;
	background: none;
	font: inherit;
	position: relative;
	cursor: pointer;
	height: var(--toggle-button-height);
	height: var(--toggle-button-height);
	width: calc(10px * 2.6);
	width: var(--toggle-button-width);
	z-index: 2;
	top: calc(50 - var(--toggle-button-height));
	top: calc(50 - var(--toggle-button-height));
	border: none;
	display: none;
	padding: 0;
	margin: 0;
}

.toggleBar {
	margin-top: 0;
}

.toggleBar::after {
	margin-top: calc(var(--toggle-button-height) / 2.5);
	margin-top: calc(var(--toggle-button-height) / 2.5);
}

.toggleBar::before {
	margin-top: calc(calc(var(--toggle-button-height) / 2.5) * -1);
	margin-top: calc(calc(var(--toggle-button-height) / 2.5) * -1);
}

.toggleBar,
.toggleBar::after,
.toggleBar::before {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
	-webkit-transition: all 0.5s ease;
	transition: all 0.5s ease;
	background: var(--toggle-button-color);
	background: var(--toggle-button-color);
	border-radius: 4px;
	border-radius: var(--border-radius);
	content: '';
	height: calc(var(--toggle-button-height) / 5);
	height: calc(var(--toggle-button-height) / 5);
	width: 100%;
}

/* screen reader only - only visible to screen readers */

.srOnly {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.rightContainer {
	display: none;
	width: calc(calc(10px * 2.6) + calc(10px * 2));
	width: calc(var(--toggle-button-width) + calc(var(--grid-size) * 2));
	height: var(--toggle-button-height);
	height: var(--toggle-button-height);
	margin: auto 10px;
	margin: auto var(--grid-size);
}

.mainMenuToggle {
	position: absolute;
	clip: rect(0, 0, 0, 0);
}

/* small screen */

@media (max-width: 768px) {
	.root {
		position: inherit;
	}

	.toggleButton {
		display: inline-block;
	}

	.menu {
		position: absolute;
		left: -100vw;
		top: calc(10px * 6);
		top: var(--header-size);
		height: 100%;
		width: 100%;
		overflow-x: visible;
		-webkit-transition: left 0.3s ease, -webkit-box-shadow 0.3s ease;
		transition: left 0.3s ease, -webkit-box-shadow 0.3s ease;
		transition: left 0.3s ease, box-shadow 0.3s ease;
		transition: left 0.3s ease, box-shadow 0.3s ease, -webkit-box-shadow 0.3s ease;
		z-index: 999;
		margin: 0;
		background: #212329;
		background: var(--header-background-color);
	}

	.menuList {
		margin: 0;
		padding: 2.5em 0 0;
		-webkit-box-shadow: -8px 0 8px var(--shadow-color);
		        box-shadow: -8px 0 8px var(--shadow-color);
		box-shadow: -8px 0 8px var(--shadow-color);
		min-height: 100%;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		padding: 10px calc(10px * 2);
		padding: var(--grid-size) calc(var(--grid-size) * 2);
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		    -ms-flex-direction: column;
		        flex-direction: column;
	}

	.menuItem {
		text-transform: uppercase;
		text-align: left;
		margin: 0;
		padding: 10px 0;
		padding: var(--grid-size) 0;
	}

	.centerContainer {
		-webkit-box-flex: 2;
		    -ms-flex-positive: 2;
		        flex-grow: 2;
		text-align: center;
	}

	.leftContainer {
		-webkit-box-flex: 0;
		    -ms-flex-positive: 0;
		        flex-grow: 0;
	}

	.rightContainer {
		display: block;
		-webkit-box-flex: 0;
		    -ms-flex-positive: 0;
		        flex-grow: 0;
	}

	.mainMenuToggle:checked ~ .left .toggleBar {
		background: transparent;
	}

	.mainMenuToggle:checked ~ .left .toggleBar::after,
	.mainMenuToggle:checked ~ .left .toggleBar::before {
		background: var(--toggle-button-color);
		background: var(--toggle-button-color);
		margin-top: 0;
		top: 0;
	}

	.mainMenuToggle:checked ~ .left .toggleBar::before {
		-webkit-transform: rotate(-45deg);
		        transform: rotate(-45deg);
	}

	.mainMenuToggle:checked ~ .left .toggleBar::after {
		-webkit-transform: rotate(45deg);
		        transform: rotate(45deg);
	}

	.mainMenuToggle:checked ~ .menu {
		left: 0;
		outline: none;
		z-index: 1000;
	}

	.mainMenuToggle:checked ~ .menu .menuList {
		position: relative;
		z-index: 1000;
	}

	.mainMenuToggle:checked ~ .left {
		z-index: 999;
	}

	.link::before {
		content: none;
	}

	.link:hover {
		color: #999;
		color: var(--color-light-gray);
	}

	.playgroundMenuItem {
		display: none;
	}
}

.iconLink {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-right: calc(10px * 1.5);
	margin-right: calc(var(--grid-size) * 1.5);
	width: 28px;
	height: 28px;
	opacity: 0.8;
}

.iconLink:hover {
	opacity: 1;
}
