:root {
	/* Color Palette */
	--color-white: #fff;
	--color-off-white: #aaa;
  --color-light: #eee;
	--color-light-gray: #999;
	--color-grey: #a3a3a3;
	--color-dark-gray: #373a3e;
	--color-black: #111;
  --color-dark: #4a00cc;
	--color-red: #dc0005;
  --color-deep-orange: #ff2800;
  --color-orange: #ff7a00;
  --color-amber: #ffaf00;
  --color-yellow: #fadc00;
  --color-lime: #dfdc00;
  --color-light-green: #95cc0d;
  --color-green: #13b20b;
  --color-teal: #339985;
  --color-cyan: #00b1cc;
  --color-light-blue: #6da7d1;
  --color-blue: #0d7ecc;
	--color-dark-blue: #3b4eb8;
  --color-indigo: #3b4eb8;
  --color-deep-purple: #639;
  --color-purple: #c30c70;
  --color-pink: #eb2f59;
  --color-brown: #7a5653;
  --color-blue-grey: #617c8f;
	--color-border: rgba(0, 0, 0, 0.1);

	--font-color: #373a3e;

	--font-color: var(--color-dark-gray);
	--font-color-hover: #111;
	--font-color-hover: var(--color-black);
	--font-color-inverse: #aaa;
	--font-color-inverse: var(--color-off-white);
	--font-color-inverse-hover: #fff;
	--font-color-inverse-hover: var(--color-white);
	--link-color: #ff2800;
	--link-color: var(--color-deep-orange);
	--link-color-hover: #eee;
	--link-color-hover: var(--color-light);

	--dark-background-color: #212329;
	--darker-background-color: #141414;

	/* Typography */
	--body-font-size: 15px;

	--block-quote-font-size: 15px;
	--block-quote-font-weight: 500;

	--pre-font-size: 13px;

	--header1-font-size: 25px;
	--header1-line-height: 1.2;

	--header2-font-size: 21px;
	--header2-line-height: 1.3;

	--header3-font-size: 19px;
	--header3-line-height: 1.4;

	--header4-font-size: 19px;
	--header4-line-height: 1.4;

	--header5-font-size: 17px;
	--header5-line-height: 1.4;

	--header6-font-size: 15px;
	--header6-line-height: 1.4;

	--table-first-column-font-size: 15px;

	--feature-points-font-size: 15px;

	/* App Variables */
	--border-radius: 4px;
	--grid-size: 10px;
	--card-padding: calc(10px * 2);
	--card-padding: calc(var(--grid-size) * 2);

	--header-background-color: #212329;

	--header-background-color: var(--dark-background-color);
	--coloresponsive-header-background-color: #141414;
	--coloresponsive-header-background-color: var(--darker-background-color);
	--hero-background: #17191c;
	--menu-offset: calc(10px * 2);
	--menu-offset: calc(var(--grid-size) * 2);
	--toggle-button-width: calc(10px * 2.6);
	--toggle-button-width: calc(var(--grid-size) * 2.6);
	--header-size: calc(10px * 6);
	--header-size: calc(var(--grid-size) * 6);

	--menu-height: calc(10px * 4.4);

	--menu-height: calc(var(--grid-size) * 4.4);

	--content-top-padding: calc(10px * 6);

	--content-top-padding: calc(var(--grid-size) * 6);
	--content-padding: calc(10px * 3);
	--content-padding: calc(var(--grid-size) * 3);
	--console-background-color: #141414;
	--console-background-color: var(--darker-background-color);
}

@media (max-width: 768px) {
	:root {
		--header1-font-size: 24px;
		--header2-font-size: 22px;
		--header3-font-size: 20px;
		--header4-font-size: 18px;
		--header5-font-size: 16px;
		--header6-font-size: 14px;
	}
}

@media (max-width: 850px) {
	:root {
		--header-size: calc(10px * 5);
		--header-size: calc(var(--grid-size) * 5);
		--content-top-padding: 10px;
		--content-top-padding: var(--grid-size);
		--content-padding: 10px;
		--content-padding: var(--grid-size);
	}
}

.root {
	z-index: 1;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	background: #212329;
	background: var(--dark-background-color);
	color: #fff;
	color: var(--color-white);
	padding: calc(10px * 3) 10px calc(10px * 4) 10px;
	padding: calc(var(--grid-size) * 3) var(--grid-size) calc(var(--grid-size) * 4) var(--grid-size);
	-webkit-box-shadow: 1px 8px 8px 0px rgba(0,0,0,0.8);
	        box-shadow: 1px 8px 8px 0px rgba(0,0,0,0.8);
}

.logo {
	width: calc(10px * 20);
	width: calc(var(--grid-size) * 20);
	margin-bottom: calc(10px * 2);
	margin-bottom: calc(var(--grid-size) * 2);
}

.title {
	text-transform: uppercase;
	color: #aaa;
	color: var(--font-color-inverse);
	line-height: 3;
}

.contentRow,
.linksRow {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.content {
	width: 90%;
	max-width: calc(10px * 126);
	max-width: calc(var(--grid-size) * 126);
	margin: 0 auto;
}

.links,
.copyright,
.linksWrapper {
	position: relative;
	width: 100%;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.linksWrapper {
	-webkit-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
}

.links {
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 33.333333%;
	        flex: 0 0 33.333333%;
	max-width: 33.333333%;
}

.linksLast {
	-webkit-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
}

.copyright {
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 33.333333%;
	        flex: 0 0 33.333333%;
	max-width: 33.333333%;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}

.link {
	color: white;
	line-height: 1.75;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}

.inactive {
	text-decoration: none;
	opacity: .4;
}

.link:visited {
  color: #c30c70;
  color: var(--color-purple);
}

.link:hover {
	color: #fff;
	text-decoration: none;
}

.linksWrapper {
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 66.666666%;
	        flex: 0 0 66.666666%;
	max-width: 66.666666%;
	position: relative;
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.externalLink {
	width: calc(10px * 1.2);
	width: calc(var(--grid-size) * 1.2);
	height: calc(10px * 1.2);
	height: calc(var(--grid-size) * 1.2);
	margin-left: calc(10px * 0.5);
	margin-left: calc(var(--grid-size) * 0.5);
}

.link .externalLink {
	opacity: 0.5;
}

.link:hover .externalLink {
	opacity: 1;
}

@media (max-width: 1200px) {
	.copyright {
		-webkit-box-flex: 0;
		    -ms-flex: 0 0 100%;
		        flex: 0 0 100%;
		max-width: 100%;
	}

	.linksWrapper {
		-webkit-box-flex: 0;
		    -ms-flex: 0 0 100%;
		        flex: 0 0 100%;
		max-width: 100%;
	}

	.links {
		margin-top: calc(10px * 2);
		margin-top: calc(var(--grid-size) * 2);
	}
}

@media (max-width: 900px) {
	.links {
		-webkit-box-flex: 0;
		    -ms-flex: 0 0 50%;
		        flex: 0 0 50%;
		max-width: 50%;
	}
}

@media (max-width: 768px) {
	.root {
		padding: calc(10px * 3) calc(10px * 2) calc(10px * 4)
			calc(10px * 2);
		padding: calc(var(--grid-size) * 3) calc(var(--grid-size) * 2) calc(var(--grid-size) * 4)
			calc(var(--grid-size) * 2);
	}

	.content {
		width: 100%;
	}

	.contentRow {
		-webkit-box-orient: vertical;
		-webkit-box-direction: reverse;
		    -ms-flex-direction: column-reverse;
		        flex-direction: column-reverse;
	}

	.copyright {
		-webkit-box-align: center;
		    -ms-flex-align: center;
		        align-items: center;
		margin-top: calc(10px * 4);
		margin-top: calc(var(--grid-size) * 4);
	}
}

@media (min-width: 1025px) {
	.wrapper {
		margin-left: calc(10px * 25);
		margin-left: calc(var(--grid-size) * 25);
		width: calc(100% - 10px * 25);
		width: calc(100% - var(--grid-size) * 25);
	}
}
